<template>
	<div>
		<div class="btn">
			<el-button type="primary" @click="dialogGrade = true">添加年级</el-button>
		</div>

		<el-dialog title="添加年级" :visible.sync="dialogGrade">
			<div class="box">
				<el-form :model="form">
					<div v-for="(item,index) in form.grades" :key="index">
						<el-form-item label="添加年级">
							<el-input v-model="item.title"></el-input>
						</el-form-item>
				<!-- 		<el-form-item label="添加班级">
							<el-input v-model="item.title"></el-input>
							<el-button type="success" @click="addGrade">添加班级</el-button>
							<el-button type="danger" @click="delGrade">删除班级</el-button>
						</el-form-item> -->
					</div>
					<el-form-item>
						<el-button type="success" @click="addGrade">添加年级</el-button>
						<el-button type="danger" @click="delGrade">删除年级</el-button>
					</el-form-item>
					<div class="submit">
						<el-form-item>
							<el-button type="primary" @click="newGrade">提交</el-button>
						</el-form-item>
					</div>
				</el-form>
			</div>
		</el-dialog>

		<el-table :data="tableDate" ref="multipleTable">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="年级ID" align="center"></el-table-column>
			<el-table-column prop="title" label="年级名称" align="center"></el-table-column>
			<el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center">
				<template slot-scope="scope">
					<el-button size="mini" type="danger" @click="handleDel(scope.$index,scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper"
			 :total="totalPage">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import API from '@/api/student.js'

	export default {
		name: 'gradems',
		data() {
			return {
				dialogGrade: false,
				form: {
					grades: []
				},
				tableDate: [],
				classList: [],
				currentPage: 1,
				totalPage: 0
			}
		},
		mounted() {
			this.getGrade();
		},
		methods: {
			// 获取年级列表
			getGrade() {
				var self = this;
				API.grades(self.currentPage).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
				})
			},
			// 添加班级
			addGrade() {
				var self = this;
				self.form.grades.push({})
			},
			delGrade() {
				var self = this;
				self.form.grades.pop({})
			},
			newGrade() {
				var self = this;
				// if(self.title) {
				// 	var _title = {
				// 		title: self.title
				// 	}
				// 	self.form.grade.push(_title)
				// }
				API.grade(self.form).then(res => {
					self.$message.success('提交成功')
					self.dialogGrade = false;
					self.getGrade();
					self.form = {};
					self.currentPage = 1
				})
			},
			// 操作
			handleDel() {},

			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getGrade();
			},
		}
	}
</script>

<style>
</style>
